import React, { Component } from "react";
import { connect } from "react-redux";
import { CircularProgress, Button, Backdrop } from "@material-ui/core";
import url from "../../strings/url";
import { Result } from "antd";
import io from "socket.io-client";
import { isAuthRequest } from "../../ApiRequests/request";
import Verify from "../VerifyMobile/VerifyMobile";
class Authenticate extends Component {
  constructor(props) {
    super(props);
    this.state = { isLoading: true };
    this.socket = undefined;
  }
  componentWillUnmount() {
    this.socket.disconnect();
  }
  componentDidMount() {
    this.socket = io.connect(`${url}`);
    if (this.props.isAuth) {
      return this.setState({ isLoading: false });
    }
    const token = localStorage.getItem("AnonymityToken#123");
    if (token !== "false") {
      isAuthRequest(token)
        .then(async (res) => {
          if (res.status === 200) {
            let user = await res.json();
            user = { ...user, token };
            this.props.dispatch({ type: "setUser", user });
            this.props.dispatch({ type: "auth" });

            this.setState({ isLoading: false });
            return;
          }
          this.setState({ isLoading: false });
        })
        .catch((err) => {
          this.setState({ isLoading: false });
        });
    } else {
      this.setState({ isLoading: false });
    }
  }
  render() {
    return (
      <div>
        <Backdrop style={{ zIndex: 1000 }} open={this.state.isLoading}>
          <CircularProgress
            style={{ color: "black", width: 100, height: 100, zIndex: 100 }}
          ></CircularProgress>
        </Backdrop>
        {this.props.isAuth ? (
          this.props.verified ? (
            <this.props.Component
              socket={this.socket}
              {...this.props}
            ></this.props.Component>
          ) : (
            <Verify
              history={this.props.history}
              open={true}
              email={this.props.email}
              dispatch={this.props.dispatch}
            ></Verify>
          )
        ) : this.state.isLoading ? (
          ""
        ) : (
          <Result
            status="403"
            title="403"
            subTitle="Sorry Not Authorize Please LogIn First"
            history={this.props.history}
            extra={
              <Button
                onClick={() => {
                  this.props.history.push("/login");
                }}
              >
                Go To Log In Screen
              </Button>
            }
          ></Result>
        )}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isAuth: state.authReducer,
    email: state.userReducer.email,
    verified: state.userReducer.verified,
  };
};
export default connect(mapStateToProps)(Authenticate);
