import React, { useState, Fragment, useEffect } from "react";
import { Modal } from "semantic-ui-react";
import validator from "validator";
import { Button, CircularProgress, Snackbar } from "@material-ui/core";
import url from "../../strings/url";

import "./index.css";
import { logoutall } from "../../ApiRequests/request";
import moment from "moment";

export const TextStyle = {
  fontFamily: "poppins",
  fontWeight: "bold",
  color: "grey",
};

const Verify = (props) => {
  const [otpSent, setOtpSent] = useState(false);
  const [otpSentLoading, setOSL] = useState(false);
  const [email, setEmail] = useState(props.email || "");
  const [otp, setOtp] = useState("");
  const [error, setError] = useState(false);
  const [already, setAlready] = useState(false);
  const [timeLeft, setTimeLeft] = useState(10);
  const [verifyLoading, setVerifyLoading] = useState(false);
  const handleVerifyOTP = (token, email, otp) => {
    setVerifyLoading(true);
    verifyOtp(token, email, otp)
      .then((res) => {
        if (res.ok) {
          return window.location.reload(false);
        }
        if (res.status === 500) {
          setError("Internal Server Error");
          setVerifyLoading(false);
          return;
        }
        setVerifyLoading(false);
        return setError("Wrong OTP or OTP Expired");
      })
      .catch((e) => {
        setError("Internal Server Error");
      });
  };

  const handleSendOtp = (token, email) => {
    setOSL(true);
    sentOtp(token, email)
      .then(async (res) => {
        if (res.ok) {
          setOSL(false);
          return setOtpSent(true);
        }
        if (res.status === 403) {
          setError("Email Already Taken");
          return setOSL(false);
        }
        setOSL(false);
        if (res.status === 409) {
          setOtpSent(true);
          setAlready(true);
          const response = await res.json();
          setEmail(response.email);
          const TL =
            10 - moment(new Date()).diff(response.generatedAt, "minutes");
          setTimeLeft(TL);
        }
      })
      .catch((e) => {
        setOSL(false);
        setError("Error Sending OTP");
      });
  };
  return (
    <Fragment>
      <Snackbar
        onClose={() => {
          setError(false);
        }}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        autoHideDuration={3000}
        open={error}
        ContentProps={{ style: { fontFamily: "poppins", fontWeight: "bold" } }}
        message={error}
      ></Snackbar>
      <Modal open={props.open} dimmer="inverted">
        <Modal.Header style={{ ...TextStyle }}>Verify Email...</Modal.Header>
        {!otpSent ? (
          <Fragment>
            <Modal.Content style={{ ...TextStyle }}>
              <input
                placeholder="Email"
                type="text"
                className="mobField comingFromLeftP5"
                value={email}
                disabled={otpSentLoading}
                style={{
                  border:
                    error === "invalid Email" ? "0.5px solid red" : "none",
                  marginRight: error === "invalid Email" ? 10 : 0,
                  transition: "0.5s",
                  borderRadius: error === "invalid Email" ? 0 : 10,
                }}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
              ></input>
              {error === "invalid Email" ? "Invalid Email" : ""}
            </Modal.Content>
            <Modal.Content>
              <Button
                variant="outlined"
                className="comingFromLeftP5"
                style={{ width: 200, color: "black", height: 35 }}
                disabled={otpSentLoading || error === "invalid Email"}
                onClick={() => {
                  if (!validator.isEmail(email)) {
                    return setError("invalid Email");
                  }
                  handleSendOtp(
                    localStorage.getItem("AnonymityToken#123"),
                    email
                  );
                }}
              >
                {otpSentLoading ? (
                  <CircularProgress
                    style={{
                      color: "black",
                      width: 10,
                      height: 10,
                      marginLeft: 10,
                    }}
                  ></CircularProgress>
                ) : (
                  "Sent Otp"
                )}
              </Button>
            </Modal.Content>
          </Fragment>
        ) : (
          <Fragment>
            <Modal.Content style={{ ...TextStyle }}>
              <div style={{ margin: 10 }}>
                {(already
                  ? "Otp Has Already Been Generated For Email ----- "
                  : "Otp Generated for ----- ") + email}
              </div>
              <input
                className="mobField comingFromRightP5"
                placeholder="Enter 6-Digit OTP"
                value={otp}
                onChange={(e) => {
                  if (
                    (validator.isNumeric(e.target.value) &&
                      e.target.value.length < 7) ||
                    e.target.value === ""
                  ) {
                    setOtp(e.target.value);
                  }
                }}
              ></input>
            </Modal.Content>
            <Modal.Content>
              <Button
                className="comingFromRightP5"
                variant="contained"
                color="secondary"
                style={{ width: 200, color: "white", height: 35 }}
                onClick={() => {
                  handleVerifyOTP(
                    localStorage.getItem("AnonymityToken#123"),
                    email,
                    otp
                  );
                }}
                disabled={otp.length !== 6}
              >
                {verifyLoading ? (
                  <CircularProgress
                    style={{ color: "white", width: 10, height: 10 }}
                  ></CircularProgress>
                ) : (
                  "Verify Email"
                )}
              </Button>
              <div
                style={{
                  ...TextStyle,
                  padding: 20,
                  cursor: "pointer",
                  textAlign: "center",
                  color: "grey",
                }}
              >
                To Resend OTP or Change Email Try Again After {timeLeft}{" "}
                minutes...
              </div>
            </Modal.Content>
          </Fragment>
        )}
        <Modal.Actions>
          <Button
            color="secondary"
            onClick={() => {
              props.history.push("/");
              logoutall();
              localStorage.setItem("AnonymityToken#123", null);
              props.dispatch({ type: "notAuth" });
            }}
          >
            Log Out
          </Button>
        </Modal.Actions>
      </Modal>
    </Fragment>
  );
};

const sentOtp = (token, email) => {
  const headers = new Headers();
  headers.append("Authorization", token);
  headers.append("content-type", "application/json");
  return fetch(`${url}/user/sentotp/${email}`, { method: "GET", headers });
};

const verifyOtp = (token, email, otp) => {
  const headers = new Headers();
  headers.append("Authorization", token);
  headers.append("content-type", "application/json");
  return fetch(`${url}/user/verifyotp/${email}/${otp}`, {
    method: "GET",
    headers,
  });
};

export default Verify;
